@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: Rubik, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #d8e1ea;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #b7c0c8;
}

h4 {
  font-weight: 400;
}

p {
  color: #798189;
}

.user-card-container {
  width: 400px;
  display: flex;
  flex-direction: row;
  padding: 15px 25px;
  cursor: pointer;
}

.divider {
  height: 6px;
}

.user-info-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 20px;
}

.user-card-container:hover {
  background-color: #f3f6f9;
}

.avatar {
  width: 50px;
  height: 50px;
  border-radius: 100px;
}

.card-list {
  min-width: 450px;
  flex: 1 1;
  overflow-y: scroll;
  box-sizing: border-box;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  flex-direction: column;
  align-self: center;
  border: 1px solid #798189;
  border-radius: 5px;
  margin-top: 15px;
}

.search-bar {
  width: 455px;
  box-sizing: border-box;
  padding: 15px 0px;
  outline: none;
  border: none;
  border-bottom: 3px solid #798189;
  font-family: Rubik;
  font-weight: 400;
  font-size: 16px;
}

.search-bar:focus {
  border-bottom: 3px solid #ff548f;
}

.app-container {
  box-sizing: border-box;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2.5em 5em;
}

